<template>
  <div>
    <project-task-board v-if="taskViewMode === 1" :projectID="projectID" :versionID="versionID" @onViewTaskItem="viewTaskItem"></project-task-board>
    <project-task-gannt v-if="taskViewMode === 2" :projectID="projectID" :versionID="versionID" ></project-task-gannt>
    <project-task-create-dialog :taskID="taskItemID"></project-task-create-dialog>
    <Drawer  @on-close="onPanelClose" title="" :value="rightSidePanelShow" :inner="true" :transfer="false" :width="30" :mask="false">
       <project-task-filter :projectID="project" :versionID="versionID"></project-task-filter>
    </Drawer>
    <Drawer  @on-close="onPanelClose" title="" v-model="showTaskDetail"  :transfer="false" :inner="true" :width="60" :mask="true">
      <project-task-info :taskID="taskItemID"></project-task-info>
    </Drawer>
  </div>
</template>

<script>
  import { mapGetters, mapMutations } from 'vuex'
  import ProjectTaskCreateDialog from './ProjectTaskCreateDialog.vue'
  import ProjectTaskInfo from './ProjectTaskInfo.vue'
  import ProjectTaskBoard from './ProjectTaskBoard.vue'
  import ProjectTaskFilter from './ProjectTaskFilter.vue'
  import ProjectTaskGannt from './ProjectTaskGannt.vue'
  import Modal from "../../../../node_modules/iview/src/components/modal/modal.vue";


  export default {
    name: 'projectTaskView',
    props: {
      projectID: {
        type: [Number,String],
        defalut: 0
      }
    },
    data () {
      return {
        columnItemHeight: 200,
        taskItemID: 0,
        showTaskDetail: false
      }
    },
    computed: {
      ...mapGetters('task', ['taskChange','taskFilterStatus','taskFilterOwners','taskFilterKeyword','taskFilters','taskGanntMaxSize']),
      ...mapGetters('projectglobal', ['projectVersion','rightSidePanelShow', 'taskViewMode']),

      versionID: function () {
        if (this.projectVersion) {
          return this.projectVersion
        } else {
          return this.$route.params.versionID
        }
      },
      project: function () {
        let result = 0
        if( this.projectID )
        {
           result = this.projectID
        }
        return result
      }

    },
    methods:
      {
        ...mapMutations('task', ['setTaskChange','setTaskGanntMaxSize']),
        ...mapMutations('projectglobal', ['setViewDialogShow','setRightPanelShow','setProjectVersion']),

        onPanelClose (){
          this.setRightPanelShow(false)
        },

        viewTaskItem (taskID)
        {
          this.showTaskDetail = true
          this.taskItemID = parseInt(taskID)
        }

      },
    created: function () {
      console.log("tttt")
    },
    mounted: function () {
    },
    watch: {
    },
    components: {
      Modal,
      ProjectTaskCreateDialog,
      ProjectTaskBoard,
      ProjectTaskFilter,
      ProjectTaskGannt,
      ProjectTaskInfo
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .board-column-item {
    margin-bottom: 5px;
    margin-top: 5px;
    min-height: 74px;
    max-height: 200px;
    width: 280px;
  }

  .board-item-priority {
    width: 1px;
    display: inline-block;
    float: left;
    height:170px;
  }

  .board-item-body {
    width: 235px;
    display: inline-table;
    word-wrap: break-word;
    white-space: initial;
    padding: 10px;

  }

  .board-item-rightbar {
    display: inline-table;
  }

  .board-item-avatar {
    margin-right: 15px;
    margin-top: 10px;
  }

</style>



