<template>
  <div>
    <div>
      <Form ref="editTask" :model="formItem" :label-width="80" :rules="ruleCustom">
        <FormItem label="" prop="ProjectVersions">
          <Cascader v-model="formItem.ProjectVersions" placeholder="" :data="projectVersions" @on-change="onProjectChange"
                    :filterable="true">
          </Cascader>
        </FormItem>
        <FormItem label="" prop="Title">
          <Input v-model="formItem.Title" placeholder=""/>
        </FormItem>
        <FormItem label="">
          <Select v-model="formItem.Owner" multiple filterable placeholder="">
            <Option v-for="member in projectMembers" :key="member.PMMember" :value="member.PMMember">{{ member.name }}
            </Option>
          </Select>
        </FormItem>
        <FormItem label="">
          <DatePicker type="date" placeholder="T+1" v-model="formItem.DeadLine"></DatePicker>
        </FormItem>
        <FormItem label="" prop="WorkHours">

          <Input v-model="formItem.WorkHours" :number="true" placeholder="8"/>

        </FormItem>
        <FormItem label="">
          <RadioGroup v-model="formItem.Priority">
            <Radio label="1"></Radio>
            <Radio label="2"></Radio>
            <Radio label="3"></Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label=":">
          <Input v-model="formItem.Description" type="textarea" :autosize="{minRows: 2,maxRows: 5}"
                 placeholder="Enter something..."></Input>
        </FormItem>
        <div style="border-top: 1px solid rgba(0,0,0,.1);"></div>
        <div style="font-size: 14px;padding-top: 5px;color:#495060">
          <Icon type="navicon" :size="14"></Icon>
          
        </div>
        <FormItem style="margin-left: -30px;" v-for="(item, index) in formItem.childTask.items" :key="index"
                  v-if="item.active" :prop="'childTask.items.' + index + '.value'"
                  :rules="{min:1,max:50,required: true, message: 'Item ' + index +' can not be empty',message:'1-30', trigger: 'blur'}">
          <Row>
            <Col span="2">
            <Checkbox v-model="item.status" :true-value="1" :false-value="0"></Checkbox>
            </Col>
            <Col span="18" style="text-align: center">
            <Input v-model="item.value" placeholder=""/></Col>
            <Col span="4">
            <span @click="handleRemove(index)"><Icon type="ios-trash" class="cursor-hand" :size="20"
                                                     style="margin-left: 30px; color:gray;"></Icon></span>
            </Col>
          </Row>
        </FormItem>
        <div>
          <span class="cursor-hand" @click="handleAdd"><Icon type="md-add-circle" :size="18"
                                                             color="#32be77"></Icon> <span></span></span>

        </div>
      </Form>
    </div>
    <div slot="footer" style="margin-top: 50px;margin-left: 80px;">
      <Button  type="success" style="width: 80px; height:30px;" shape="circle"
              @click="ok('editTask')">
      </Button>
      <!--<Button type="ghost"  style="width: 80px; height:30px;"  shape="circle" @click="cancel"></Button>-->
    </div>
  </div>
</template>

<script>
  import { mapGetters, mapMutations } from 'vuex'
  import { initProjectVersions, initTaskForm, taskValidateRules } from './ProjectTaskCreateDialog'
  import FormItem from '../../../../node_modules/iview/src/components/form/form-item.vue'
  import { Window, WindowInstaller} from  '@progress/kendo-window-vue-wrapper'

  export default {
    name: 'ProjectTaskInfo',
    props: {
      taskID: {
        type: Number,
        default: 0
      }
    },
    components: {
      FormItem,
      Window
    },
    data () {
      return {
        sourceProject: [],
//        projectMember: [],
        projectVersions: [],
        formItem: {
          id: this.taskID,
          Title: '',
          Owner: [],
          Status: 0,
          Priority: '1',
          DeadLine: '',
          WorkHours: 8,
          Description: '',
          ProjectID: 0,
          Version: 0,
          ProjectVersions: [],
          childTask: {
            index: 0,
            items: [
              {}
            ]
          },
          attachments: {
            defaultList: [
              {
                name: 'a42bdcc1178e62b4694c830f028db5c0',
                url: 'https://o5wwk8baw.qnssl.com/a42bdcc1178e62b4694c830f028db5c0/avatar'
              },
              {
                name: 'bc7521e033abdd1e92222d733590f104',
                url: 'https://o5wwk8baw.qnssl.com/bc7521e033abdd1e92222d733590f104/avatar'
              }
            ],
            imgName: '',
            visible: false,
            uploadList: []
          }
        },
        ruleCustom: {
          ...taskValidateRules
        }
      }

    },
    computed: {
      ...mapGetters('projectglobal', ['createDialogShow', 'viewDialogShow', 'projectVersion']),
      ...mapGetters(['appBodyHeight','userInfo']),
      containerHeight: function () {
        return this.appBodyHeight-100
      },
      dialogShow: function () {
        return (this.createDialogShow || this.viewDialogShow)
      },
      task: function () {
        if (this.createDialogShow) {
          return 0
        }
        else {
          return this.taskID
        }
      },

      formTitle: function () {
        if (this.createDialogShow) {
          return ''
        } else {
          return ''
        }
      },

      projectMembers: function () {
        let project = this.formItem.ProjectVersions[0]
        let result = []
        for (let i = 0; i < this.sourceProject.length; i++) {
          if (this.sourceProject[i].id === project) {
            result = this.sourceProject[i].Members
          }
        }
        return result
      }
    },
    methods:
      {
        ...mapMutations('projectglobal', ['setCreateDialogShow', 'setViewDialogShow', 'setObjectChange']),
        ...mapMutations('task', ['setTaskChange']),

        ok (name) {
          this.$refs[name].validate((valid) => {
            if (valid) {
              this.formItem.ProjectID = this.formItem.ProjectVersions[0]
              this.formItem.Version = this.formItem.ProjectVersions[1]
              if (this.formItem.Owner.length === 0)
              {
                this.formItem.Owner.push(this.userInfo.id)
              }
                this.$axios.put('/api/project/task/' + this.taskID + '/', this.formItem).then(response => {
                  this.setTaskChange(true)
                }, response => {
                  this.setTaskChange(true)
                })
            }
          })
        },
        cancel () {
//          this.$Message.info('Clicked cancel')
          this.setCreateDialogShow(false)
          this.setViewDialogShow(false)
        },
        handleAdd () {
          this.formItem.childTask.index++
          this.formItem.childTask.items.push({
            value: '',
            index: this.formItem.childTask.index,
            status: 0,
            active: 1
          })
//          console.log(this.formItem)
        },
        handleRemove (index) {
          this.formItem.childTask.items[index].active = 0

        },
        handleView (name) {
          this.formItem.attachments.imgName = name
          this.formItem.attachments.visible = true
        },
        handleSuccess (res, file) {
          file.url = 'https://o5wwk8baw.qnssl.com/7eb99afb9d5f317c912f08b5212fd69a/avatar'
          file.name = '7eb99afb9d5f317c912f08b5212fd69a'
        },
        handleFormatError (file) {
          this.$Notice.warning({
            title: 'The file format is incorrect',
            desc: 'File format of ' + file.name + ' is incorrect, please select jpg or png.'
          })
        },
        handleMaxSize (file) {
          this.$Notice.warning({
            title: 'Exceeding file size limit',
            desc: 'File  ' + file.name + ' is too large, no more than 2M.'
          })
        },
        handleBeforeUpload () {
        },
        onProjectChange (value, selectedData) {
          let project = value[0]
          for (let i = 0; i < this.sourceProject.length; i++) {
            if (this.sourceProject[i].id === project) {
              this.projectMember = this.sourceProject[i].Members
            }
          }
        },

        loadProjectInfo: function () {
          this.$axios.get('/api/project/list?extinfo=1&home=1').then(response => {
            let tempData = response.data.result
            this.sourceProject = tempData
            this.projectVersions = initProjectVersions(tempData)
          }, response => {
            // error callback
          })
        },

        initTaskForm: function () {
          this.formItem.childTask.items = []
          this.formItem.id = this.task
          let defaultTask = this.formItem

          if (this.formItem.id !== 0) {
            let initPromise = initTaskForm(this.taskID)
            initPromise.then(function (initData) {
              defaultTask.childTask.items= []
              defaultTask.Title = initData.Title
              defaultTask.Owner =initData.Owner
              defaultTask.Status =initData.Status
              defaultTask.Priority = initData.Priority + ''
              defaultTask.DeadLine = initData.DeadLine
              defaultTask.Description = initData.Description
              defaultTask.WorkHours = parseInt(initData.WorkHours)
              defaultTask.ProjectVersions = [initData.ProjectID,initData.Version]
              defaultTask.ProjectID = initData.ProjectID
              defaultTask.Version = initData.Version
              for (let i = 0; i < initData.Childs.length; i++) {
                defaultTask.childTask.index = defaultTask.childTask.index + 1
                let temp = {}
                temp.value = initData.Childs[i].Title
                temp.index = i + 1
                temp.status = initData.Childs[i].Status
                temp.active = initData.Childs[i].IsActive
                temp.id = initData.Childs[i].id
                defaultTask.childTask.items.push(temp)
              }
              if (initData.Childs.length === 0) {
                defaultTask.childTask.items = []
              }

            })
          }
          else {
            defaultTask.Title = ''
            defaultTask.Owner = [this.userInfo.id]
            defaultTask.Priority = '1'
            defaultTask.DeadLine = ''
            defaultTask.Desc = ''
            defaultTask.WorkHours = 8
            defaultTask.ProjectVersions = []
            defaultTask.ProjectID = 0
            defaultTask.Version = 0
            defaultTask.childTask.items = []
          }
        }
      },
    created () {
      this.loadProjectInfo()
    },
    mounted () {
//      this.formItem.attachments.uploadList = this.$refs.upload.fileList
    },
    watch: {

      task: function () {
        this.initTaskForm()
     },

      viewDialogShow: function ( value) {
        if (value)
        {
          this.initTaskForm()
        }
      },

      formItem: function () {
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .demo-upload-list {
    display: inline-block;
    width: 60px;
    height: 60px;
    text-align: center;
    line-height: 60px;
    border: 1px solid transparent;
    border-radius: 4px;
    overflow: hidden;
    background: #fff;
    position: relative;
    box-shadow: 0 1px 1px rgba(0, 0, 0, .2);
    margin-right: 4px;
  }

  .demo-upload-list img {
    width: 100%;
    height: 100%;
  }

  .demo-upload-list-cover {
    display: none;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, .6);
  }

  .demo-upload-list:hover .demo-upload-list-cover {
    display: block;
  }

  .demo-upload-list-cover i {
    color: #fff;
    font-size: 20px;
    cursor: pointer;
    margin: 0 2px;
  }
</style>



